import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import FetchHelper from "../../helpers/FetchHelper";
import { Loading } from "../layouts/Loading";

export default function ViewArticle(props) {
    const [loading,setLoading]=useState(false);
    const getArticle=()=>{
        setLoading(true);
        let url="article/getById/"+props.id;
        console.log(url);
        let fetch=FetchHelper.getData(url);
        fetch.then(response=>{
            console.log(response);
            setArticle(response);
        })
        .catch(error=>console.log(error))
        .finally(()=>{
            setLoading(false)
        })
    }

    const [article,setArticle]=useState({})
    const navigate=useNavigate();

    useEffect(()=>{
        getArticle();
        document.title="Article ";
    },[]);

    return(
        <div>
                    {loading ? (
                        <Loading/>
                    ):(
                        <div  className="w-full max-w-6xl rounded bg-gray-900 shadow-xl p-10 lg:p-20 mx-auto text-gray-200 relative md:text-left">
                            <div  className="md:flex items-center -mx-10">
                                <div  className="w-full md:w-1/2 px-10 mb-10 md:mb-0">
                                    <div  className="relative">
                                        <img src={`data:image/png;base64,${article.photo}`} alt={article.photo}   className="w-full relative z-10" />
                                        <div  className="border-4 border-yellow-200 absolute top-10 bottom-10 left-10 right-10 z-0"></div>
                                    </div>
                                </div>
                                <div  className="w-full md:w-1/2 px-10">
                                    <div  className="mb-10">
                                        <div  className="mb-5">
                                            <h1  className="font-bold uppercase text-2xl">{article.title}</h1>
                                            <h3  className="font-semibold text-xl">{article.description}</h3>
                                        </div>
                                        <p  className="text-sm">
                                            {article.content}
                                        </p>
                                    </div>
                                    <div>
                                        <div  className="inline-block align-bottom">
                                            <button onClick={()=>{navigate("/");window.location.reload()}}  className="bg-yellow-300 opacity-75 hover:opacity-100 text-yellow-900 hover:text-gray-900 rounded-full px-10 py-2 font-semibold"><i  className="mdi mdi-cart -ml-2 mr-2"></i>Retour</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )}
        </div>
    );
}