import { useEffect, useState } from "react";
import FetchHelper from "../../helpers/FetchHelper";
import { DataView, DataViewLayoutOptions } from 'primereact/dataview';
import { Loading } from "../layouts/Loading";
import { Button } from 'primereact/button';
import { useNavigate } from "react-router-dom";
        
        
export default function AllArticle() {

    const [article, setArticle] = useState([]);
    const [loading, setLoading] = useState(false);
    const [layout, setLayout] = useState('grid');
    const navigate=useNavigate();

    const getArticle = () => {
        setLoading(true);
        let url = "article/getAll";
        let fetch = FetchHelper.getData(url);
        fetch.then(response => {
            console.log(response);
            setArticle(response.data);
        })
        .catch(error => console.log(error))
        .finally(() => {
            setLoading(false);
        });
    }

    useEffect(() => {
        document.title="Acceuil"
        console.log("mitady");
        getArticle();
    }, []);

    const listItem = (article) => {
        return (
            <div className="col-12">
                <div className="flex flex-column xl:flex-row xl:align-items-start p-4 gap-4">
                    <img className="w-9 sm:w-16rem xl:w-10rem shadow-2 block xl:block mx-auto border-round" src={`data:image/png;base64,${article.photo}`} alt={article.title} />
                    <div className="flex flex-column sm:flex-row justify-content-between align-items-center xl:align-items-start flex-1 gap-4">
                        <div className="flex flex-column align-items-center sm:align-items-start gap-3">
                            <h1 className="text-2xl font-bold text-900">{article.title}</h1>
                            <p>{article.description}</p>
                            <div className="flex align-items-center gap-3">
                                <span className="flex align-items-center gap-2">
                                    <i className="pi pi-tag"></i>
                                    <span className="font-semibold">{article.type}</span>
                                    
                                </span>
                            </div>
                            <div className="flex sm:flex-column align-items-center sm:align-items-end gap-3 sm:gap-2">
                                <span>{article.datePub}</span>
                                <Button onClick={()=>{navigate("/article-"+article.id)}} icon="pi pi-chevron-right" className="p-button-rounded"></Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    };

    const gridItem=(article)=>{
        return (
            <div className="col-12 sm:col-6 lg:col-12 xl:col-4 p-2">
                <div className="p-4 border-1 h-full surface-border surface-card border-round">
                    <div className="flex flex-wrap align-items-center justify-content-between gap-2">
                        <div className="flex align-items-center gap-2">
                            <i className="pi pi-tag"></i>
                            <span className="font-semibold">{article.type}</span>
                        </div>
                    </div>
                    <div className="flex flex-column align-items-center gap-3 py-5">
                        <img className="w-9 shadow-2 border-round" src={`data:image/png;base64,${article.photo}`} alt={article.title} />
                        <div className="text-2xl font-bold">{article.title}</div>
                        <p>{article.description}</p>
                    </div>
                    <div className="flex align-items-center justify-content-between">
                        <span>{article.datePub}</span>
                        <Button onClick={()=>{navigate("/article-"+article.id);window.location.reload()}} icon="pi pi-chevron-right" className="p-button-rounded"></Button>
                    </div>
                </div>
            </div>
        );
    }

    const itemTemplate = (product, layout) => {
        if (!product) {
            return;
        }

        if (layout === 'list') return listItem(product);
        else if (layout === 'grid') return gridItem(product);
    };

    const header = () => {
        return (
            <div className="flex justify-content-end">
                <DataViewLayoutOptions layout={layout} onChange={(e) => setLayout(e.value)} />
            </div>
        );
    };

    return (
        <div className="card">
            {loading ? (
                <Loading /> // Afficher un indicateur de chargement pendant le chargement des données
            ) : (
                <DataView value={article} itemTemplate={itemTemplate} layout={layout} paginator rows={6} header={header()}/>
            )}
        </div>
    );
}
