import { Link } from "react-router-dom";
import image from "./../../assets/images/default.png";

export default function NavBar() {

    return(
        <header className="sticky">
            <nav
                className="
                flex flex-wrap
                justify-between
                w-full
                py-4
                md:py-0
                px-4
                text-lg text-gray-500
                bg-gray-950
                "
            >
            <div className="mt-1">
              <Link to={"/"}>
                <h1 className="cursor-pointer flex text-2xl text-gray-100"><img src={image} alt="icon" className="w-12 h-12 mr-4"/> <span className="mt-2"> InfoIA</span></h1>
              </Link>
            </div>
       
        
          
    </nav>
  </header>
    );
}